import EventEmitter, { Event } from "../core/EventEmitter";

export type TLivePlayInfo = {
    idx?: number | null,// null이면 실행 안하는 중
    totalIdx?: number,
    src?: string,
    json?: any,
    st?: number,
    et?: number,
    callback?: () => void,
    repeat?: number,
    interval?: number,
    speed?: number,
    repeatCnt?: number,
    allplay?: boolean,// 전체듣기
    // allplayModoshi?: boolean,// 전체반복
}
let audioTimeArr: number[];
class ZZAudioProgram {
    get zzaudio() {
        console.log('temp_playwhy2 getzzaudio');
        return (window as any).zzaudio;
    }
    livePlayInfo: TLivePlayInfo = {
        idx: null,
        totalIdx: 0,
        src: '',
    };
    presetSrc(src: string) {
        this.livePlayInfo.src = src;
        // this.zzaudio.presetSrc(src);
    }
    playEffect(inID: string) {
        if (!this.zzaudio) return;
        this.zzaudio.playEffect(inID);
    }
    play(param: TLivePlayInfo) {

        const that = this;
        this.livePlayInfo = Object.assign(this.livePlayInfo, param)

        const idx = this.livePlayInfo.idx = this.livePlayInfo.idx || 0;
        audioTimeArr = this.livePlayInfo.json.sentense[idx].timeen.split('|');
        // this.livePlayInfo.repeatCnt = param.repeat;
        this.livePlayInfo.callback = this.playComplete.bind(that);
        this.livePlayInfo.st = audioTimeArr[0];
        this.livePlayInfo.et = audioTimeArr[1];
        if (param.repeat !== 9999) {
            if (this.livePlayInfo.repeatCnt) this.livePlayInfo.repeatCnt--;
        }
        EventEmitter.dispatch(Event.CONTROL_UI_UPDATE, idx)
        // EventEmitter.dispatch(Event.SENTENSE_PLAYING,idx)
        this.zzaudio.play(this.livePlayInfo);
    }
    pause() {
        this.livePlayInfo.idx = null;
        this.livePlayInfo.allplay = false;
        this.livePlayInfo.repeat = 0;
        this.livePlayInfo.repeatCnt = 0;
        // EventEmitter.dispatch(Event.SENTENSE_PAUSE,this.livePlayInfo.idx)
        EventEmitter.dispatch(Event.CONTROL_UI_UPDATE, this.livePlayInfo.idx)
        if (this.zzaudio) this.zzaudio.pause();
    }
    setVolume(vol: number) {
        this.zzaudio.setVolume(vol);
    }

    async playComplete() {
        // debugger;
        // console.log('repeatCnt',livePlayInfo.repeatCnt)
        //

        const currentSoundTime = (audioTimeArr[1] - audioTimeArr[0]);
        const intervalTime = (() => {
            const settingInterval: any = this.livePlayInfo.interval;
            if (settingInterval === 9999) {
                return currentSoundTime;// 문장길이 +1초
            } else {
                return settingInterval * 1000;// 설정타임
            }
        })();
        if (this.livePlayInfo.allplay || this.livePlayInfo.repeat === 9999) {
            await delay(intervalTime);
        }

        EventEmitter.dispatch(Event.SENTENSE_PAUSE, this.livePlayInfo.idx)
        EventEmitter.dispatch(Event.CONTROL_UI_UPDATE, this.livePlayInfo.idx)

        this.livePlayInfo.repeatCnt = this.livePlayInfo.repeatCnt || 0;
        if (this.livePlayInfo.repeatCnt > 0) {
            // this.livePlayInfo.repeat = this.livePlayInfo.repeatCnt;

            this.play(this.livePlayInfo);
            return;
        }
        if (this.livePlayInfo.allplay) {
            // debugger;
            // 다음인덱스
            const idx = this.livePlayInfo.idx || 0;

            if (this.livePlayInfo.totalIdx) {
                this.livePlayInfo.idx = idx + 1;
                // 끝에 도달
                if (this.livePlayInfo.idx >= this.livePlayInfo.totalIdx) {
                    this.livePlayInfo.idx = 0;
                }
            }

            // 반복 cnt복원
            this.livePlayInfo.repeatCnt = this.livePlayInfo.repeat;

            this.play(this.livePlayInfo);
            return;
        }
        function delay(ms: number): Promise<void> {
            return new Promise<void>(resolve => {
                setTimeout(() => {
                    console.log(`${ms} 밀리초가 지났습니다.`);
                    resolve()
                }, ms);
            });
        }
        // if(repeatCnt > 0){
        //     this.play();
        //     param.repeatCnt
        // }
    }

}


export default new ZZAudioProgram();