import { padStart as _pad } from 'lodash';
import React, { useEffect, useState } from 'react';
import { useRecoilState, useSetRecoilState } from 'recoil';
import data from '../core/data';
import EventEmitter, { Event } from '../core/EventEmitter';
import { setSceneIdxState, isYTModuleState, ytbSentenseHideState } from '../core/store';
import ZZAudioProgram from './ZZAudioProgram';

// let courseIdx = 0;
const sentenseRepeat: number = 10;// 문장 반복횟수 즉 듣기+말하기 이므로 결국 (5*2)10번 재생
let courseToMainJSON: any = {};
let _cid = '';
let _tpid = '';
let _tpidx = 0;
const YTModule = ({
    cid,
    tpid,
    tpidx,
}: {
    cid: string,
    tpid: string,
    tpidx: number,
}) => {
    const setYtbSentenseHide = useSetRecoilState(ytbSentenseHideState);
    const [sceneIdx, setSceneIdx] = useRecoilState(setSceneIdxState);
    const [isYTModule, setIsYTModule] = useRecoilState(isYTModuleState);
    const [update, setUpdate] = useState(0);
    courseToMainJSON = data.courseToMainJSON(cid);
    _cid = cid;
    _tpid = tpid;
    _tpidx = tpidx;
    console.log('cid, tpid', cid, tpid);
    useEffect(() => {

        setIsYTModule(true);
        // courseIdx = data.courseToMainIdx(cid, tpid);
        EventEmitter.subscribe(Event.SENTENSE_PAUSE, () => {
            let cntForRepeat = ZZAudioProgram.livePlayInfo.repeatCnt;
            if (!cntForRepeat) cntForRepeat = sentenseRepeat * 2;
            const turnCnt = Math.ceil(cntForRepeat / 2);

            if (turnCnt > 8) {
                setYtbSentenseHide('00');

            } else if (turnCnt > 6) {
                setYtbSentenseHide('e0');

            } else {
                setYtbSentenseHide('ek');

            }
            const cnt = ZZAudioProgram.livePlayInfo.repeatCnt;
            console.log('iiinfo :: ', cnt, cnt! % 2, ZZAudioProgram.livePlayInfo.idx, ZZAudioProgram.livePlayInfo.totalIdx! - 1);
            ZZAudioProgram.setVolume(1);
            if (cnt! % 2 === 1) {
                ZZAudioProgram.setVolume(0.15);
            }
            // 마지막인 경우
            if (cnt === 0 && ZZAudioProgram.livePlayInfo.idx === ZZAudioProgram.livePlayInfo.totalIdx! - 1) {
                ZZAudioProgram.pause();
                setSceneIdx(3);
            }

            setUpdate(Math.random());
        })
    }, [])
    const ls = ZZAudioProgram.livePlayInfo.repeatCnt! % 2 === 1 ? 'speak' : 'listen';
    return (
        <div className="YTModule">

            <SceneIntro />
            <SceneOuttro />
            <SceneSentense />
            <Scene0 />

            {
                sceneIdx === 2 &&
                <>
                    <div className="yt-header">
                        <div className="yt-header-in">
                            <span className="logo">카셋 영어 회화</span>
                            <span className="divide">/</span>
                            영화 인턴편
                            <span className="divide">/</span>
                            <span className="day">DAY{_pad(String(_tpidx + 1), 3, '0')}</span>
                        </div>
                    </div>


                    <div className="yt-footer">
                        <div className="repeat-cnt">{(() => {
                            let cnt = ZZAudioProgram.livePlayInfo.repeatCnt;
                            if (!cnt) cnt = sentenseRepeat * 2;
                            return Math.ceil(cnt / 2);
                        })()}<span className="repeat-cnt-tot">/{sentenseRepeat}</span></div>
                        <div className="pill-listen-speak">
                            <div className="pill-item">듣기</div>
                            <div className="pill-item">말하기</div>
                            <div className={`pill-active ${ls}`}>{ls === 'speak' ? '말하기' : '듣기'}</div>
                        </div>

                    </div>
                </>
            }


        </div>
    );
};

const Scene0 = () => {
    const [sceneIdx, setSceneIdx] = useRecoilState(setSceneIdxState);
    const pauseAndNextstep = () => {
        setSceneIdx(sceneIdx + 1);

    }

    return (
        <>
            {sceneIdx === 0 && <div className="scene scene-0">
                <div className="scene-in scene-0-in">
                    <span className="font1">명조폰트</span>
                    <span className="font2">고딕폰트</span>
                    <button onClick={() => {
                        setTimeout(() => {
                            setSceneIdx(1);
                            // ZZAudioProgram.playEffect('mp3-effect1');

                        }, 3000);
                        data.sendTelelog('test casete hello');
                    }}>시작</button>
                </div>
            </div>}
        </>

    )
}
const SceneIntro = () => {
    // debugger;
    // courseIdx = data.courseToMainIdx(_cid, _tpid);
    console.log('courseIdx2', _cid, _tpid);
    const [init, setInit] = useState(false);
    const [innerScene, setInnerScene] = useState(0);
    const [sceneIdx, setSceneIdx] = useRecoilState(setSceneIdxState);
    if (sceneIdx === 1) {
        // 한번만 실행되도록 init추가
        if (!init) {
            setInit(true);

            setTimeout(() => {
                ZZAudioProgram.playEffect('effect_short1');
                // 이너씬 1로 전환
                setInnerScene(1);
            }, 3000);
            setTimeout(() => {

                // 메인씬 2로 전환
                setSceneIdx(2);
            }, 8000);
        }

    }
    // console.log('ZZAudioProgram.livePlayInfo', ZZAudioProgram.livePlayInfo)
    return (
        <>
            {sceneIdx === 1 && <div className="scene scene-intro">
                <div className="scene-in scene-intro-in">
                    {
                        innerScene === 0 && <div className="innerscene0">
                            <div className="section-main">
                                {/* <div className="txt0">카셋 영어 회화</div> */}
                                <div className="txt1">영화 인턴 편</div>
                                <div className="txt2"><span className="day">
                                    {_pad(String(_tpidx + 1), 3, '0')}
                                </span><div className="divide"></div><div className="tot">51</div></div>
                            </div>
                            <div className="section-footer">
                                <div>카셋 영어 회화</div>
                            </div>
                        </div>
                    }
                    {
                        innerScene === 1 && <div className="innerscene1">
                            <ul>
                                <li><i className="fas fa-check"></i>총 {courseToMainJSON.item[_tpidx].tit.length}개의 문장</li>
                                <li><i className="fas fa-check"></i>한 문장당 <div className="pill-listen-speak">
                                    <div className="pill-item">듣기</div>
                                    <div className="pill-item">말하기</div>
                                </div> {sentenseRepeat}세트로 구성</li>
                            </ul>
                            <div className="txt0">영화 인턴 <span className="pb"><i className="far fa-file-audio"></i>쉐도잉 프로그램</span>은 <br></br><span className="pb">카셋 홈페이지</span>를 방문해 주세요.</div>
                            {/* * 총 3개의 문장
                            * 1 문장당 [듣고/말하기] 5세트로 구성
                            *
                            * 영화 인턴 플레이어 북은 홈페이지를 방문해 주세요. */}
                        </div>
                    }

                </div>
            </div>}
        </>
    )
}

const SceneSentense = () => {
    const [init, setInit] = useState(false);
    const [sceneIdx, setSceneIdx] = useRecoilState(setSceneIdxState);
    if (sceneIdx === 2) {
        // 한번만 실행되도록 init추가
        if (!init) {

            setInit(true);
            setTimeout(() => {

                // 시작
                ZZAudioProgram.livePlayInfo.repeat = sentenseRepeat * 2;
                ZZAudioProgram.livePlayInfo.repeatCnt = sentenseRepeat * 2;
                ZZAudioProgram.livePlayInfo.interval = 1;

                ZZAudioProgram.play({
                    allplay: true,
                    totalIdx: data.topicJSON.sentense.length,
                    repeat: ZZAudioProgram.livePlayInfo.repeat,
                    repeatCnt: ZZAudioProgram.livePlayInfo.repeatCnt,
                })

            }, 1000);
        }


    }
    return (
        <>
            {sceneIdx === 2 && <div className="scene guide-frame">
                <div className="scene-in guide-frame-in"></div>
            </div>}
        </>
    )
}
const SceneOuttro = () => {
    const [init, setInit] = useState(false);
    const [sceneIdx, setSceneIdx] = useRecoilState(setSceneIdxState);
    if (sceneIdx === 3) {
        // 한번만 실행되도록 init추가
        if (!init) {
            setInit(true);
            ZZAudioProgram.playEffect('effect_ending');
            setTimeout(() => {
                // 이너씬 1로 전환
                alert('eot')
            }, 10000);
        }

    }

    return (
        <>
            {sceneIdx === 3 && <div className="scene scene-outtro">
                <div className="scene-in scene-outtro-in">
                    <div>
                        <div className="txt0"><i className="fas fa-check"></i>영상이 마음에 드셨다면 <span className="em">좋아요,구독</span> 잊지 마세요!</div>
                        {/* <div className="txt1">채널을 운영하는 힘이 됩니다!</div> */}
                        <div className="txt1"><i className="fas fa-check"></i>영화 인턴 <span className="em"><i className="far fa-file-audio"></i>쉐도잉 프로그램</span>은 <span className="pb">카셋 홈페이지</span>방문!</div>
                    </div>
                </div>
            </div>}
        </>
    )
}

export default YTModule;