const GotoNextPrev = ({
    cid,
    tpid,
}: {
    cid: string,
    tpid: string,
}) => {
    return (<ul className="GotoNextPrev">
        <li
            onClick={() => {
                data.gotoNextPrev(tpidx, cid, 'prev');
                data.gotoTop();
                setTimeout(() => {
                    window.location.reload();
                }, 100);
            }}
            className="btn-list prev"><i className="far fa-arrow-alt-circle-left icon"></i>이전목록</li>
        <li
            onClick={() => {
                data.gotoCourseMainPage(cid);
            }}
            className="btn-list list"><i className="fas fa-list-ul icon"></i>전체목록</li>
        <li
            onClick={() => {
                data.gotoNextPrev(tpidx, cid, 'next');
                data.gotoTop();
                setTimeout(() => {
                    window.location.reload();
                }, 100);
            }}
            className="btn-list next">다음목록<i className="far fa-arrow-alt-circle-right icon"></i></li>
    </ul>);
}

