const PlayLists = ({
    cid,
    tpid,
}: {
    cid: string,
    tpid: string,
}) => {
    useEffect(() => {
        EventEmitter.subscribe(Event.CONTROL_UI_UPDATE, (idx: number) => {

            // setActiveIdx(idx);
            setRender(render + Math.random());
        });
        // EventEmitter.subscribe(Event.SENTENSE_PAUSE, () => {
        //     setActiveIdx(-1);
        // });
        return () => {
            EventEmitter.remove(Event.CONTROL_UI_UPDATE);
            EventEmitter.remove(Event.SENTENSE_PAUSE);
        };
    }, []);

    const isCloseDetailPanel = useRecoilValue(isCloseDetailPanelState);
    const [render, setRender] = useState(0);
    // const [activeIdx, setActiveIdx] = useState(-1);

    const sentense = data.topicJSON.sentense;
    const Component = sentense.map((item: any, i: number) => {
        return <SentenseItem
            idx={i}
            totalIdx={sentense.length}
            cid={cid} tpid={tpid}
            renderCnt={render}
            // isActive={activeIdx === i}
            json={item}
            key={`sentense${i}`}
        />;
    });
    // console.log('tmplog_fbase.userMoreInfo', fbase.userMoreInfo)
    // const [forceRender, setForceRender] = useState(0);
    // callModeChange = () => {
    //     setForceRender(forceRender + 1);
    // }
    console.log('rrrrrrender-ddd')
    return (
        <div className={classnames(["PlayLists", css`
            padding-bottom:${isCloseDetailPanel ? 30 : 267}px;
        `])}>
            {Component}
        </div>
    )
}

const SentenseItem = ({

    // isActive = false,
    renderCnt,
    json,
    idx,
    totalIdx,
    cid,
    tpid,
}: {

    // isActive: boolean,
    renderCnt: number,
    json: any,
    idx: number,
    totalIdx: number,
    cid: string,
    tpid: string,
}) => {

    console.log('rrrrrrender renderCnt', ZZAudioProgram.livePlayInfo.idx);
    const [isOpenDesc, setIsOpenDesc] = useState(false);
    const [isGlassMode, setIsGlassMode] = useState(false);
    const scriptMode = useRecoilValue(scriptModeState);
    const [isCheck, setIsCheck] = useState(fbase.userMoreInfo.doneSentense.includes(`${tpid}_${idx}`));

    const checkClick = (tf: boolean) => {

        if (!fbase.isLogin()) {
            data.showToastMessage({
                message: "로그인이 필요한 서비스입니다.",
                mtime: 3000
            })
            EventEmitter.dispatch(Event.FB_START_LOGIN, {
                from: "buyProduct",
                courseID: json.courseID,
            });
            return;
        }
        setIsCheck(tf);

        const makeKey = `${tpid}_${idx}`;

        data.setSettingKeepClipSentense(
            'done',
            tf ? 'add' : 'remove',
            makeKey
        )
        // data.setSettingKeepClipSentense(
        //     'sentense',
        //     tf ? 'add' : 'remove',
        //     makeKey
        // )
        // 문장을 저장
        // data.setSettingKeepSentenseMoreInfo(
        //     tf ? 'add' : 'remove',
        //     makeKey,
        //     json.en
        // );

    }
    const isActive = idx === ZZAudioProgram.livePlayInfo.idx ? true : false;
    const words = json.words;
    const setIsShowShadowing = useSetRecoilState(isShowShadowingState);
    const setIsShowSpeedmate = useSetRecoilState(isShowSpeedmateState);
    // console.log('jsonjsonjson',json);
    // const timeArr = json.timeen.split('|');
    // const isOnePlay: boolean = (isActive && ZZAudioProgram.livePlayInfo.repeat === 9999)?false:true;
    const isInfinityPlay: boolean = (isActive && ZZAudioProgram.livePlayInfo.repeat === 9999) ? true : false;
    const playClick = () => {

        console.log('tmplog_playa');
        ZZAudioProgram.play({
            idx: idx,
            totalIdx: totalIdx,
            src: audioSrc,
            repeat: 1,
            repeatCnt: 1,
            allplay: false,
        });
    }

    const ytbSentenseHide = useRecoilValue(ytbSentenseHideState);
    const isYTModule = useRecoilValue(isYTModuleState);
    let ytbHideClassEn = '';
    let ytbHideClassKo = '';
    if (isYTModule) {

        if (ZZAudioProgram.livePlayInfo.idx === null) {
            // 최초에 아직 결정되기 전에도 blur로 처리
            ytbHideClassEn = 'blur';
            ytbHideClassKo = 'blur';
        }

        console.log('dioProgram.livePlayInf', idx, ZZAudioProgram.livePlayInfo.idx);

        if (isActive) {
            // 활성화 & 하이드 턴이면
            ytbHideClassEn = ytbSentenseHide.substr(0, 1) === '0' ? 'blur' : '';
            ytbHideClassKo = ytbSentenseHide.substr(1, 1) === '0' ? 'blur' : '';
        } else if (idx > ZZAudioProgram.livePlayInfo.idx!) {
            // 아직 진행 전 문장이면
            ytbHideClassEn = 'blur';
            ytbHideClassKo = 'blur';
        }

    }

    return (
        <div onTouchStart={() => {
            data.sentensePressing();
        }} className={classnames([`SentenseItem num-${idx}`, { active: isGlassMode }, { active: isActive }])}>

            <div className={classnames(["item-sound", { active: isActive }])}>
                <div className="item-main">
                    <div className="col-left" onClick={() => {
                        playClick();
                        data.sentensePressing();
                        // data.setCurrPlayBtnState('stop', 'a4');// 현재 실행되고 있는 경우 하단 메인 컨트롤을 멈추기위해서
                        // setTimeout(() => {
                        //     // 1개 반복
                        //     playOneItem();
                        // })

                    }}>
                        <ItemCnt idx={idx} />
                        <div>
                            {
                                isGlassMode
                                    ?
                                    <>
                                        <div className="title-en">{json.en}</div>
                                        <div className="title-ko">{json.ko}</div>
                                    </>
                                    :
                                    <>
                                        {['ek', 'e0'].includes(scriptMode) && <div className={classnames(["enko title-en", ytbHideClassEn])}>{json.en}</div>}
                                        {/* {['_k'].includes(scriptMode) && <div className={classnames(["title-en_", ytbHideClassEn])}>{json.en_}</div>} */}
                                        {['0k', '00'].includes(scriptMode) && <div className={classnames(["enko title-en_0", ytbHideClassEn])}>{json.en_0}</div>}
                                        {['00', 'e0'].includes(scriptMode) && <div className={classnames(["enko title-ko_0", ytbHideClassKo])}>{json.ko_0}</div>}
                                        {['_k', 'ek', '0k'].includes(scriptMode) && <div className={classnames(["enko title-ko", ytbHideClassKo])}>{json.ko}</div>}
                                    </>
                            }
                        </div>
                    </div>
                    <div className="col-right">
                        {/* {idx} */}


                    </div>
                </div>
                <div className="item-bottom">
                    <div>
                        <div className="col">
                            <button onClick={() => {
                                // data.setCurrPlayBtnState('stop', 'a3');// 현재 실행되고 있는 경우 하단 메인 컨트롤을 멈추기위해서
                                // setTimeout(() => {
                                //     // 1개 반복
                                //     playOneItem();
                                // })
                                playClick();
                            }} className="btn">
                                <i className="fas fa-volume-up">
                                    <span className="i-label">듣기</span>
                                </i>
                            </button>
                            <button className="btn">
                                {
                                    isInfinityPlay ?
                                        <div className="stop-repeat" onClick={() => {

                                            ZZAudioProgram.pause();
                                        }}>
                                            <Equalizer />
                                            <span className="i-label">반복종료</span>
                                        </div>
                                        :
                                        <div onClick={() => {

                                            ZZAudioProgram.play({
                                                idx: idx,
                                                totalIdx: totalIdx,
                                                src: audioSrc,
                                                repeat: 9999,
                                                repeatCnt: 9999,
                                                allplay: false,
                                            });
                                        }} >
                                            <i className="fas fa-retweet"></i><span className="i-label">무한반복</span>
                                        </div>
                                }
                                {/* {!(sentenseRepeatIdx === idx) && <i onClick={() => {
                                    data.setCurrPlayBtnState('stop', 'a2');// 현재 실행되고 있는 경우 하단 메인 컨트롤을 멈추기위해서
                                    setTimeout(() => {
                                        temprepeatMode = fbase.userMoreInfo.repeatMode;
                                        fbase.userMoreInfo.repeatMode = '1';
                                        data.setCurrPlayBtnState('play');
                                        // // setIsRepeat(true);
                                        data.audioPlayPause(idx);
                                        // repeat equalizer보이기 인덱스
                                        setSentenseRepeatIdxState(idx);
                                    })
                                }} className="fas fa-retweet">
                                    <span className="i-label">무한반복</span>
                                </i>}
                                {sentenseRepeatIdx === idx && <div className="stop-repeat" onClick={() => {
                                    data.setCurrPlayBtnState('stop', 'a1');
                                    fbase.userMoreInfo.repeatMode = temprepeatMode;
                                    // setIsRepeat(false);
                                    // repeat equalizer보이기 인덱스
                                    setSentenseRepeatIdxState(9999);
                                }} >
                                    <Equalizer />
                                    <span className="i-label">반복종료</span>
                                </div>} */}

                            </button>
                            <button className="btn">
                                {isCheck
                                    ?
                                    <i className="fas fa-check-square checkbox check" onClick={() => {
                                        checkClick(false);
                                    }}><span className="i-label">학습완료</span></i>
                                    :
                                    <i className="fas fa-check-square checkbox" onClick={() => {
                                        checkClick(true);
                                    }}><span className="i-label">학습중</span></i>
                                }
                            </button>
                        </div>
                        <div className="col">
                            {words && <button className="btn btn-word" onClick={() => {
                                setIsOpenDesc(!isOpenDesc);
                            }}><i className="fas fa-plus">
                                    <span className="i-label">단어({words.length})</span></i>
                            </button>}

                            <button onClick={() => {
                                data.pageShadowingParam.sentenseEn = json.en;
                                data.pageShadowingParam.sentenseKo = json.ko;
                                data.pageShadowingParam.sentenseIdx = idx;
                                data.pageShadowingParam.totalIdx = totalIdx;
                                data.pageShadowingParam.audioSrc = audioSrc;
                                data.pageShadowingParam.topicID = tpid;

                                setIsShowShadowing(true);
                                // setIsShowSpeedmate(true);

                            }} className="btn btn-sndtraining">
                                <span className="logo-snd">SD</span><span className="i-label">강화훈련</span>
                            </button>
                            {['_k', 'e0', '0k', '00'].includes(scriptMode) && <button className={classnames(["btn btn-glass", { glasson: isGlassMode }])} onClick={() => {

                                setIsGlassMode(true);
                                setTimeout(() => {
                                    setIsGlassMode(false);
                                }, 3000);
                            }}><i className="fas fa-eye i-con"><span className="i-label">잠깐보기</span></i>
                            </button>}
                        </div>
                    </div>
                    {isOpenDesc && words && <div>
                        <WordList json={words} modeClass="" />
                    </div>}
                </div>
                {isYTModule && isActive && words && <div>
                    <WordList json={words} modeClass="yt-words" />
                </div>}
            </div>
        </div >
    );
}

