const WordList = ({
    json,
    modeClass,
}: {
    json: any,
    modeClass: string,
}) => {
    const Itm = json.map((item: string[], i: number) => {
        return <li className="item-word">
            <span className="word">{item[0]}</span>
            <span className="mean">{item[1]}</span>
        </li>
    })
    return (
        <ul className={classnames(["items-word", modeClass])}>
            {Itm}
        </ul>
    );
}

const ItemCnt = ({
    idx,
}: {
    idx: number,
}) => {
    return (
        <span className="item-cnt">
            {idx + 1}
        </span>
    )
}

