import classnames from 'classnames';
import { padStart as _pad } from 'lodash';
import React from 'react';
import data from '../core/data';
// import core from '../core/Core';
import EventEmitter, { Event } from '../core/EventEmitter';
import fbase from '../fb/fbase';
// import fbase from '../fb/fbase';

const WCourseItem = ({
    viewType,
    itemJSON,
    courseID,
    idx,
}: {
    viewType: string,
    itemJSON: any,
    courseID: string,
    idx: number,
}) => {
    // console.log('WCourseItem',viewType,itemJSON,idx);
    if (viewType === 'movie') {
        console.log('WCourseItem', itemJSON.tit);
    }
    // const isAvailableBook = fbase.isAvailableBook(courseID); // needPurchase, available, needLogin
    const isSubscriptionInfo = fbase.isSubscriptionInfo();
    const getSubscriptionState = data.getSubscriptionState(isSubscriptionInfo);
    console.log('isSubscriptionInfo', isSubscriptionInfo)
    return (
        <>
            {
                viewType === 'movie'
                    ?
                    <CItemMovie isSubscriptionInfo={getSubscriptionState} courseID={courseID} itemJSON={itemJSON} idx={idx} key={'CItemMovie' + idx} />
                    :
                    <CItem isSubscriptionInfo={getSubscriptionState} courseID={courseID} itemJSON={itemJSON} idx={idx} key={'CItem' + idx} />
            }
            {/* <CItem itemJSON={itemJSON} idx={idx} key={idx} /> */}
        </>
    );
};

const CItemMovie = (
    {
        itemJSON,
        courseID,
        isSubscriptionInfo,
        idx
    }: {
        itemJSON: any,
        courseID: string,
        isSubscriptionInfo: string,
        idx: number
    }) => {
    const Tits = itemJSON.tit.map((str: string, i: number) => {
        return <div key={i} className="sentense">{i + 1}. {str}</div>
    })
    const idxPad = _pad(String(idx + 1), 3, '0');
    const isTrial = itemJSON.isTrial === 'true';

    return (
        <li className="CourseItem movie" onClick={() => {
            // alert(itemJSON.query);

            if (!isTrial && isSubscriptionInfo !== 'on') {
                // alert('구매내역이 존재하지 않습니다. 구매 후 이용 가능합니다.')
                // if (process.env.NODE_ENV === 'development') {
                //     EventEmitter.dispatch(Event.SHOW_ALERT, {
                //         moduleName: "AlertBuyProduct",
                //         courseID: courseID,
                //     });
                // }

                // return;

                if (isSubscriptionInfo === 'needLogin') {
                    data.showToastMessage({
                        message: "로그인이 필요한 서비스입니다.",
                        mtime: 3000
                    });
                    EventEmitter.dispatch(Event.FB_START_LOGIN, {
                        from: "buyProduct",
                        courseID: courseID,
                    });
                } else {
                    // alert('구매내역이 존재하지 않습니다. 구매 후 이용 가능합니다.')
                    EventEmitter.dispatch(Event.SHOW_ALERT, {
                        moduleName: "AlertBuyProduct2",
                        courseID: courseID,
                    });
                }
                return;
            }
            // core.setParameter(itemJSON.query);
            data.gotoPage('CoursePlay', itemJSON.query)
        }} key={idx}>

            <div className={classnames(["group-img", { trial: isTrial }])}>
                {/* {isAvailableBook} */}
                {isTrial && isSubscriptionInfo !== 'on' && <div className="mark-trial">
                    무료보기
                </div>}
                <img className={classnames(["img", { trial: isTrial }])} src={`/resource/courses/${courseID}/thumb/a${idxPad}.jpg`}></img>
                <div className="sceneno">
                    <div className="t1">DAY</div>
                    <div className="t2">{idxPad}</div>
                </div>
            </div>

            <div className="sentenses">{Tits}</div>

        </li>
    );
}
const CItem = (
    {
        itemJSON,
        courseID,
        isSubscriptionInfo,
        idx
    }: {
        itemJSON: any,
        courseID: string,
        isSubscriptionInfo: string,
        idx: number
    }) => {
    const Tits = itemJSON.tit.map((str: string, i: number) => {
        return <div key={i} className="sentense">{i + 1}. {str}</div>
    })
    const idxPad = _pad(String(idx + 1), 3, '0');
    const isTrial = itemJSON.isTrial === 'true';

    return (
        <li className="CourseItem normal" onClick={() => {
            // alert(itemJSON.query);

            if (!isTrial && isSubscriptionInfo !== 'on') {
                // alert('구매내역이 존재하지 않습니다. 구매 후 이용 가능합니다.')
                // if (process.env.NODE_ENV === 'development') {
                //     EventEmitter.dispatch(Event.SHOW_ALERT, {
                //         moduleName: "AlertBuyProduct",
                //         courseID: courseID,
                //     });
                // }

                // return;

                if (isSubscriptionInfo === 'needLogin') {
                    data.showToastMessage({
                        message: "로그인이 필요한 서비스입니다.",
                        mtime: 3000
                    });
                    EventEmitter.dispatch(Event.FB_START_LOGIN, {
                        from: "buyProduct",
                        courseID: courseID,
                    });
                } else {
                    // alert('구매내역이 존재하지 않습니다. 구매 후 이용 가능합니다.')
                    EventEmitter.dispatch(Event.SHOW_ALERT, {
                        moduleName: "AlertBuyProduct2",
                        courseID: courseID,
                    });
                }
                return;
            }
            // core.setParameter(itemJSON.query);
            data.gotoPage('CoursePlay', itemJSON.query)
        }} key={idx}>

            <div className={classnames(["group-img", { trial: isTrial }])}>
                {isTrial && isSubscriptionInfo !== 'on' && <div className="mark-trial">
                    무료보기
                </div>}

                <div className="sceneno">
                    <span className="t1">DAY</span>
                    <span className="t2">{idxPad}</span>
                </div>
            </div>

            <div className="sentenses">
                <div className="sentense">[{itemJSON.tit[0]}]</div>
                <div className="sentense">{itemJSON.tit[1]}</div>
            </div>

        </li>
    );
}
export default WCourseItem;