    isLogin() {
        let r = true;
        if (this.userInfo === null || this.userInfo === 'loading') {
            r = false;
        }
        return r;
    }
    isSubscriptionInfo(): string {

        // 로그인이 안 된 경우
        if (!this.isLogin()) {
            return 'needLogin';
        }
        if (!this.userMoreInfo.subscriptionInfo) {
            return 'needPurchase';
        }
        return this.userMoreInfo.subscriptionInfo;
    }

