    sendPasswordResetEmail(email: string) {
        var auth = firebase.auth();
        auth.sendPasswordResetEmail(email).then(function () {
            alert(email + '로 발송된 이메일을 확인 해 주세요.')
        }).catch((error) => {
            console.log(error);
            alert(this.convertErrorMessage(error));
        });
    }
    convertErrorMessage(error: any) {
        const errorMessage: any = {
            'auth/invalid-email': '올바른 이메일을 입력하세요.',
            'auth/invalidNewPw': '입력한 비밀번호가 일치하지 않습니다.',
            'auth/weak-password': '6자리 이상의 비밀번호를 입력하세요',
            'auth/wrong-password': '비밀번호가 올바르지 않습니다.',
            'auth/user-not-found': '해당 가입정보가 없습니다. 정보를 확인 또는 회원가입을 해주세요.',
            'auth/email-already-in-use': '이미 가입된 이메일입니다.',

        }
        const convertMessage = errorMessage[error.code]
        if (convertMessage) {
            return convertMessage;
        }
        console.log(error);
        return error.message;
    }
