import classnames from 'classnames';
import React, { useEffect, useState } from 'react';
import EventEmitter, { Event } from '../core/EventEmitter';
import fbase, { authService } from './fbase';
// let tempLoginPopupParam: {
//     from: string,
//     courseID: string,
// } | null = null;
const btnLabel: any = {
    'login': '로그인',
    'join': '가입신청',
    'findpw': '이메일로 링크보내기',
};
const FBViewLogout = () => {
    const [inputEmail, setInputEmail] = useState('');
    const [inputPw, setInputPw] = useState('');
    const [inputPw2, setInputPw2] = useState('');
    // const [newAccount, setNewAccount] = useState(false);
    const [isShowLoginPopup, setisShowLoginPopup] = useState(false);
    const [viewMode, setViewMode] = useState('login');// login, join, findpw
    useEffect(() => {
        EventEmitter.subscribe(Event.FB_START_LOGIN, (options: any) => {
            // tempLoginPopupParam = options;
            setisShowLoginPopup(true);
        })
    }, []);
    const onChange = (event: any) => {
        const {
            target: { name, value },
        } = event;
        if (name === "email") {
            setInputEmail(value);
        } else if (name === "pw") {
            setInputPw(value);
        } else if (name === "pw2") {
            setInputPw2(value);
        }
    }
    const onSubmit = async (e: any) => {
        e.preventDefault();
        if (!inputEmail) {
            alert('이메일을 입력해주세요.')
            return;
        }
        // 신규가입인경우 비번 두번입력 비교
        if (viewMode === 'join' && (inputPw !== inputPw2)) {
            alert(fbase.convertErrorMessage({
                code: 'auth/invalidNewPw'
            }));
            return;
        }
        try {
            let data;
            if (viewMode === 'join') {
                // create account
                await authService.createUserWithEmailAndPassword(inputEmail, inputPw);
            } else if (viewMode === 'login') {
                // log in
                await authService.signInWithEmailAndPassword(inputEmail, inputPw);
            } else if (viewMode === 'findpw') {
                // 비번 찾기
                authService.sendPasswordResetEmail(inputEmail).then(() => {
                    alert(`입력하신 ${inputEmail}로 발송한 메일을 확인해주세요.`)
                }).catch((error: any) => {

                    if (error.code === 'auth/user-not-found') {
                        alert(fbase.convertErrorMessage({
                            code: error.code
                        }));
                    } else {
                        alert('ERROR : ' + error.message);
                    }
                });

            }

        } catch (error) {
            //   setError(error.message);
            console.log(error);
            alert(fbase.convertErrorMessage(error));
            // alert(error.message);
        }
    }

    // if (!isShowLoginPopup) {
    //     if (tempLoginPopupParam !== null && tempLoginPopupParam.from === 'buyProduct') {
    //         EventEmitter.dispatch(Event.FB_START_LOGIN, {
    //             from: "buyProduct",
    //             courseID: tempLoginPopupParam.courseID,
    //         });
    //     }
    //     tempLoginPopupParam = null;
    // }
    return (
        <div className="FBViewLogout">
            <div>
                <button onClick={() => {
                    setisShowLoginPopup(true);
                }} className="btn btn-login"><i className="fas fa-user-circle i-left"></i>로그인</button>


            </div>
            {
                isShowLoginPopup &&
                <div className="popup-login">
                    <div className="popup-login-in">

                        <div className="popup-header">
                            <h3 className="center">

                                <div className="nav">
                                    <div className="nav-tabs">
                                        <div onClick={() => {
                                            setViewMode('login');
                                        }} className={classnames(["nav-item", { "active": viewMode === "login" }])}>
                                            로그인
                                                <div className="underline"></div>
                                        </div>
                                        <div onClick={() => {
                                            setViewMode('join');
                                        }} className={classnames(["nav-item", { "active": viewMode === "join" }])}>
                                            회원가입
                                                <div className="underline"></div>
                                        </div>
                                    </div>
                                </div>
                                {
                                    ['findpw'].includes(viewMode) && <div className="tit-findpw">비밀번호 찾기 </div>
                                }
                            </h3>
                            <div className="right">
                                <button onClick={() => {
                                    setisShowLoginPopup(false);
                                }} className="btn btn-close"><i className="fas fa-times"></i></button>
                            </div>
                        </div>

                        <form onSubmit={onSubmit}>
                            <ul>
                                <li>
                                    <input className="form-styling"
                                        placeholder="이메일 입력"
                                        autoCapitalize="false"
                                        name="email" type="text" onChange={onChange} value={inputEmail}></input>
                                </li>
                                {
                                    ['login', 'join'].includes(viewMode) &&
                                    <li>
                                        <input className="form-styling"
                                            placeholder="비밀번호 입력"
                                            name="pw" type="password" onChange={onChange} value={inputPw}></input>
                                    </li>
                                }
                                {
                                    ['join'].includes(viewMode) &&
                                    <li>
                                        <input className="form-styling"
                                            placeholder="비밀번호 확인"
                                            name="pw2" type="password" onChange={onChange} value={inputPw2}></input>
                                    </li>
                                }

                            </ul>
                            {/* <table>
                                <tbody>
                                    <tr>
                                        <td>이메일</td>
                                        <td>
                                            <input className="form-styling" name="email" type="text" onChange={onChange} value={inputEmail}></input>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>비밀번호</td>
                                        <td>
                                            <input className="form-styling" name="pw" type="password" onChange={onChange} value={inputPw}></input>
                                        </td>
                                    </tr>
                                    {
                                        newAccount && <>
                                            <tr>
                                                <td>비밀번호 확인</td>
                                                <td>
                                                    <input className="form-styling" name="pw2" type="password" onChange={onChange} value={inputPw2}></input>
                                                </td>
                                            </tr>
                                        </>
                                    }
                                </tbody>
                            </table> */}
                            <button className="btn-login">{btnLabel[viewMode]}</button>
                        </form>
                        {
                            ['login', 'join'].includes(viewMode) &&
                            <div className="footer-pw">
                                <button className="btn btn-underline" onClick={() => {
                                    // setNewAccount(true);
                                    setViewMode('findpw')
                                    // fbase.sendPasswordResetEmail(inputEmail);
                                }}>비밀번호를 잊으셨나요?</button>
                            </div>
                        }

                        {/* <AuthLoginUI /> */}
                    </div>
                    {/* <FindPassword /> */}
                </div>

            }


        </div>
    );
};

const AuthLoginUI = () => {

    const [stepCD, setStepCD] = useState('signin');

    return (
        <div className="AuthLoginUI">
            <div className="kareui-template-04 kareui-wrapper">

                <div className="section-top">
                    <div className="btn-group">
                        <div className="nav nav-tabs">
                            <div onClick={() => {
                                setStepCD('signin');
                            }} className={classnames(["nav-item", { "active": stepCD === "signin" }])}>
                                로그인
					        </div>
                            <div onClick={() => {
                                setStepCD('signup');
                            }} className={classnames(["nav-item", { "active": stepCD === "signup" }])}>
                                회원가입
					        </div>
                        </div>
                        <div className="nav-active">
                            <div className={classnames(["underline", stepCD])}></div>
                        </div>
                    </div>
                </div>


                <div className={`tab-content ${stepCD}`}>

                    <div className={classnames(["tab-pane", { "active": stepCD === "signin" }])} id="signin">

                        <form className="section form-box" action="" method="post" name="form">
                            {/* <label htmlFor="username">사용자이메일</label> */}
                            <input className="form-styling" type="text" name="username" placeholder="사용자 이메일" />
                            <label htmlFor="password">비밀번호</label>
                            <input className="form-styling" type="text" name="password" placeholder="비밀번호" />

                            <button className="btn-login">
                                로그인하기
					</button>
                        </form>

                        <div className="forgot">
                            <a href="#">비밀번호를 잊으셨나요?</a>
                        </div>
                    </div>
                    <div className={classnames(["tab-pane", { "active": stepCD === "signup" }])} id="signup">
                        <form className="section form-box" action="" method="post" name="form">

                            <input className="form-styling" type="text" name="username" placeholder="사용자 이메일" />

                            <input className="form-styling" type="text" name="email" placeholder="비밀번호" />

                            <input className="form-styling" type="text" name="password" placeholder="비밀번호 확인" />

                            <button className="btn-login">
                                회원가입하기
					</button>
                        </form>

                        <div className="forgot">
                            <a href="#">비밀번호를 잊으셨나요?</a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

