import classnames from 'classnames';
import { send } from 'process';
import QRCode from "qrcode.react";
import React, { useState } from 'react';
import {
    BrowserView, isBrowser,
    MobileView
} from "react-device-detect";
import { atom, useRecoilState, useRecoilValue, useSetRecoilState } from 'recoil';
import data, { productTable } from '../core/data';
import Days from '../core/Days';
import fbase from '../fb/fbase';

const paymentStepState = atom({
    key: "paymentStepState",
    default: 0,
})
const selectPlanState = atom({
    key: "selectPlanState",
    default: '',
})

const sendTelemessage = ({
    sendTitle,
    email,
    uid,
    priceCode,
    moreStr,
}: {
    sendTitle: string,
    email: string
    uid: string
    priceCode: string,
    moreStr: string,
}) => {
    data.sendTelelog(`${sendTitle}::${email}##${uid}##${priceCode}##${moreStr}`)
}
const AlertBuyProduct2 = ({
    openParam
}: {
    openParam: any
}) => {

    const paymentStep = useRecoilValue(paymentStepState);

    return (
        <div className="AlertBuyProduct2">
            {paymentStep === 0 && <PayStep01 />}
            {paymentStep === 1 && <PayStep02 />}
        </div>
    );
};

const PayStep01 = () => {
    const setPaymentStep = useSetRecoilState(paymentStepState);
    const [selectPlan, setSelectPlan] = useRecoilState(selectPlanState);
    return (<div className="PayStep01">
        <div className="header">
            <div className="title">
                구독기간 선택
                </div>
            <button className="btn btn-close" onClick={() => {
                data.hideAlert();
            }}><i className="fas fa-times"></i></button>
        </div>
        <div className="notice">
            안녕하세요. 해당 서비스를 만든 개발자입니다.<br></br>
                부득이하게 서버 운영을 위해 전체 코스는 <br></br>소정의 비용을 받고 있습니다. <br></br>
                이용의 개선점이나 불편함은 언제든 <a href="mailto:dev@casete.in">이메일</a> 및 <a href="http://pf.kakao.com/_HxlMJK/chat" target="_blank">카카오채널</a>로 알려주세요. <br></br>

        </div>
        <ul className="plan-list">
            <li className={classnames(["btn-plan-radio", { active: selectPlan === '1m' }])} onClick={() => {
                setSelectPlan('1m');
            }}>
                <div className="bullet"><i className="fas fa-check"></i></div>
                <div>1개월 구독<div className="price">{data.numberWithCommas(productTable['1m'].price)}원<span className="per-month">(=2,400원/월)</span></div></div>

            </li>
            <li className={classnames(["btn-plan-radio", { active: selectPlan === '3m' }])} onClick={() => {
                setSelectPlan('3m');
            }}>
                <div className="bullet"><i className="fas fa-check"></i></div>
                <div>3개월 구독<div className="price">{data.numberWithCommas(productTable['3m'].price)}원<span className="per-month">(=2,000원/월)</span></div></div>

            </li>
            <li className={classnames(["btn-plan-radio", { active: selectPlan === '6m' }])} onClick={() => {
                setSelectPlan('6m');
            }}>
                <div className="bullet"><i className="fas fa-check"></i></div>
                <div>6개월 구독<div className="price">{data.numberWithCommas(productTable['6m'].price)}원<span className="per-month">(=1,500원/월)</span></div></div>

            </li>
            <li className={classnames(["btn-plan-radio", { active: selectPlan === '12m' }])} onClick={() => {
                setSelectPlan('12m');
            }}>
                <div className="bullet"><i className="fas fa-check"></i></div>
                <div>12개월 구독<div className="price">{data.numberWithCommas(productTable['12m'].price)}원<span className="per-month">(=1,200원/월)</span></div></div>
            </li>
        </ul>
        <div className="footer">
            <button className={classnames(["btn-confirm", { active: selectPlan !== '' }])} onClick={() => {
                if (selectPlan === '') {
                    alert('구독기간을 선택해 주세요.');
                    return;
                }
                // console.log('fbase.userMoreInfo', fbase.userMoreInfo)
                sendTelemessage({
                    sendTitle: '결제진행버튼',
                    email: fbase.userMoreInfo.email,
                    uid: fbase.userMoreInfo.uid,
                    priceCode: selectPlan,
                    moreStr: '',
                });
                setPaymentStep(1)
            }}>결제진행</button>
        </div>

    </div>);
}

const PayStep02 = () => {
    const [buyStep, setBuyStep] = useState(0);
    const [stepCD, setStepCD] = useState(isBrowser ? 1 : 0);
    const [selectPlan, setSelectPlan] = useRecoilState(selectPlanState);
    if (isBrowser) {
        // 브라우져는 바로 30초 카운팅
        setTimeout(() => {
            setStepCD(2);
        }, 15 * 1000);
    }
    const qrurl = 'https://qr.kakaopay.com/281006012180682200001379' + productTable[selectPlan].priceQR;
    return (<div className="PayStep02">
        <div className="header">
            <div className="title">
                <img className="kakaopay-logo" src="https://img.etnews.com/photonews/2009/1337259_20200915103744_396_0001.jpg"></img>카카오페이 송금결제
                </div>
            <button className="btn btn-close" onClick={() => {
                data.hideAlert();
            }}><i className="fas fa-times"></i></button>
        </div>

        <div className="message">
            {productTable[selectPlan].title} 구독( {data.numberWithCommas(productTable[selectPlan].price)}원 )
            </div>

        <div className="step step1">

            <div className="step1-in">
                <BrowserView>
                    <ul>
                        <li className="title">
                            <div className="bullet-no">1</div>
                            휴대폰 카카오톡으로 아래의 QR코드 스캔 및 결제 후,<br></br>
                            <div className="small">
                                <div>카카오톡 송금 창 메세지 입력란에</div>
                                <div>가입하신 이메일({fbase.userMoreInfo.email})을 꼭 입력해 주세요.</div>
                            </div>

                        </li>
                        <li className="qr-container">
                            {
                                stepCD > 0
                                    ?
                                    <QRCode value={qrurl} />
                                    :
                                    <div className="btn-create-qr" onClick={() => {
                                        setStepCD(1);

                                    }}>
                                        <div className="cover">
                                            <i className="fas fa-redo-alt"></i>
                                            <div className="desc">송금코드생성</div>
                                        </div>
                                        <div className="dummy"><QRCode value="qr코드 생성용 로딩입니다" /></div>
                                    </div>
                            }

                        </li>
                        <li className="title"><div className="bullet-no">2</div> 결제 후 승인요청 버튼을 눌러주세요.</li>
                    </ul>

                </BrowserView>
                <MobileView>
                    <ul>
                        <li className="title">
                            <div className="bullet-no">1</div>
                            카카오톡 송금 창 메세지 입력란에 가입하신 이메일(macaoshu@gmail.com)을 꼭 입력해 주세요.
                        </li>
                        <li className="qr-container">
                            <a onClick={() => {
                                setTimeout(() => {
                                    setStepCD(2);
                                }, 15 * 1000);
                            }} className="btn-gotokakao" href={qrurl} target="_blank">카카오페이 결제하기<i className="fas fa-angle-right"></i></a>
                        </li>
                        <li className="title"><div className="bullet-no">2</div> 결제 후 승인요청 버튼을 눌러주세요.</li>
                    </ul>

                </MobileView>
                {/* <div>반드시 메세지에 가입하신 이메일 주소를 입력 해 주세요.</div> */}
            </div>
        </div>

        <div className="step step2">
            <div className="notice">
                문제가 있는경우 <a href="mailto:dev@casete.in">이메일</a> 및 <a href="http://pf.kakao.com/_HxlMJK/chat" target="_blank">카카오채널</a>로 문의 주세요.
                </div>
            <button className={classnames(["btn-confirm", `stepCD-${stepCD}`])} onClick={() => {
                if (stepCD < 2) {
                    alert('결제 완료 후 승인요청을 해주세요.');
                    return;
                }
                alert('승인이 완료되면(결제 후 평균 15분 이내 승인) 내정보 > 구입목록에서 확인하실 수 있습니다. ')
                const monNum = ({
                    "1m": 1,
                    "3m": 3,
                    "6m": 6,
                    "12m": 12
                } as any)[selectPlan];
                const todayStr = Days.utc().local().format('YYYYMMDD');
                const enddayStr = Days(todayStr).add(monNum, 'month').format('YYYYMMDD')
                const fbasesubscriptionInfo = `t${selectPlan}##${todayStr}##${enddayStr}`;
                fbase.userMoreInfo.subscriptionInfo = fbasesubscriptionInfo;
                sendTelemessage({
                    sendTitle: '승인요청',
                    email: fbase.userMoreInfo.email,
                    uid: fbase.userMoreInfo.uid,
                    priceCode: selectPlan,
                    moreStr: fbasesubscriptionInfo,
                });
                fbase.syncMoreInfo();
                setTimeout(() => {
                    data.hideAlert();
                    window.location.reload();
                }, 1000);
            }}>승인요청</button>
        </div>

    </div>);
}

// export default AlertBasic;
export default AlertBuyProduct2;